export function toArrayBuffer(file) { return new Promise(function (res) { var fileReader = new FileReader(); fileReader.onload = function (event) { res(event.target.result); }; fileReader.readAsArrayBuffer(file); }); }
export function toDataURL(file) { return new Promise(function (res) { var fileReader = new FileReader(); fileReader.onload = function (event) { res(event.target.result); }; fileReader.readAsDataURL(file); }); }
